
.App{
  background: #446CB3;
    text-align: center;
    padding: 70px 0;
}
input[type="email"] {
  width: 480px;
  margin-top: 20px;
  padding: 10px;
  font-size: 30px;
  border: none;
  border-bottom: solid 2px #c9c9c9;
  -webkit-transition: border 0.3s;
  -o-transition: border 0.3s;
  transition: border 0.3s;
}

input[type="password"] {
  width: 480px;
  margin-top: 20px;
  padding: 10px;
  font-size: 30px;

  border: none;
  border-bottom: solid 2px #c9c9c9;
  -webkit-transition: border 0.3s;
  -o-transition: border 0.3s;
  transition: border 0.3s;
}
input[type="email"]:focus,
input[type="password"].focus {
  border-bottom: solid 2px #969696;
}

button {
  font-size: 30px;
  background-color: #00B16A;
  color: #fff;
  padding: 10px 30px;
  margin-top: 20px;
  margin-right: 10px;

}
button:hover{
  background-color: #1c875b;
}

p#red{
  color: #E26A6A;
  text-decoration: none;
}
p#green{
  color: #87D37C;
  text-decoration: none;
}

.hide{
  display: none;
}

.google{
  font-size: 30px;
  background-color: #dd4b39;
  color: #fff;
  padding: 10px 30px;
  margin-top: 20px;
  margin-right: 10px;
}



